import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			loginType: '',
			phone: '',
			password: '',
			logining: false
		}
	},
	onShow() {
		this.$api.logout()
	},
	onLoad(options) {
		
	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		chooseLoginType(type) {
			this.loginType = type
		},
		navBack() {
			uni.navigateBack();
		},
		toRegist() {
			uni.redirectTo({
				url: '/pages/public/register'
			})
		},
		async toLogin() {
			const that = this
			if (that.phone.length !== 11) {
				that.$api.msg('请输入11位中国手机号')
			} else if (that.password.length < 8) {
				that.$api.msg('密码至少8位')
			} else {
				that.logining = true;

				//若是小程序平台，则获取到openId。整个过程是静默完成的
				uni.login({
					provider: 'weixin',
					success: (wxres => {
						that.$api.request('user', 'login', {
							phone: that.phone,
							password: that.password,
							loginType: 1,
							raw: JSON.stringify(wxres)
						}, failres => {
							that.logining = false
							uni.showToast({
								title: failres.errmsg,
								icon: "none"
							});
						}).then(res => {
							that.logining = false
							that.$store.commit('login', res.data)
							uni.setStorageSync('userInfo', res.data)
							if (that.$api.prePage().lodaData) {
								that.$api.prePage().loadData()
							}
							uni.navigateBack()
						})
					})
				})






















			}
		},
		miniWechatLogin(e) {
			const that = this
			that.logining = true
			let loginType = 1
			let userInfo = e.detail.userInfo
			uni.login({
				provider: 'weixin',
				success: (wxres => {
					that.logining = false
					that.$api.request('user', 'thirdPartLogin', {
						loginType: loginType,
						raw: JSON.stringify(wxres)
					}, failres => {
						that.$api.msg(failres.errmsg)
						uni.hideLoading()
					}).then(res => {
						that.$api.setUserInfo(res.data)
						that.$api.request('user', 'syncUserInfo', userInfo).then(syncRes => {
							//同步过后
							res.data.nickname = userInfo.nickName
							res.data.avatarUrl = userInfo.avatarUrl
							res.data.gender = userInfo.gender
							uni.setStorageSync('userInfo', res.data)
							that.$store.commit('login', res.data)
							that.$api.setUserInfo(res.data)

							if (that.$api.prePage().lodaData) {
								that.$api.prePage().loadData()
							}
							uni.hideLoading()
							uni.navigateBack()
						})
					})
				})
			})



		},
		wechatLogin() {
			const that = this
			that.logining = true
			let loginType = 2
			uni.showLoading({
				title: '正在同步消息'
			})
			uni.login({
				provider: 'weixin',
				success: (wxres => {
					that.$api.request('user', 'thirdPartLogin', {
						loginType: loginType,
						raw: JSON.stringify(wxres)
					}, failres => {
						that.$api.msg(failres.errmsg)
						uni.hideLoading()
					}).then(res => {
						that.logining = false
						uni.getUserInfo({
							lang: 'zh_CN',
							success: (e) => {
								uni.setStorageSync('userInfo', res.data)
								that.$store.commit('login', res.data)
								e.userInfo.nickname = e.userInfo.nickName
								that.$api.request('user', 'syncUserInfo', e.userInfo).then(syncRes => {
									//同步过后
									res.data.nickname = e.userInfo.nickName
									res.data.avatarUrl = e.userInfo.avatarUrl
									res.data.gender = e.userInfo.gender
									uni.setStorageSync('userInfo', res.data)
									that.$store.commit('login', res.data)
								})
							},
							complete: (e) => {
								if (that.$api.prePage().lodaData) {
									that.$api.prePage().loadData()
								}
								uni.hideLoading()
								uni.navigateBack()
							}
						})
					})
				})
			})
		},
		wechatH5Login() {
			const that = this
			let href = window.location.origin
			let page = that.$api.prePage()
			let prePath = '/pages/index/index'
			if (page) {
				prePath = page.__page__.path
			}
			window.location = 'https://open.weixin.qq.com/connect/oauth2/authorize?' 
			+ 'appid=' + that.$api.defConfig().h5Appid + '&redirect_uri=' + escape(href) + '&response_type=code&scope=snsapi_userinfo&state=' + escape(prePath) + '#wechat_redirect'
		}
	},

}
